import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SponsorsComponent } from '../views/sponsors/sponsors.component';
import { SponsorsContainerComponent } from '../views/sponsors-container/sponsors-container.component';
import { sponsorsDataResolver } from '../resolvers/sponsors.resolver';
import { SponsorsInfoComponent } from '../views/info/info.component';
import { sponsorsInfoDataResolver } from '../resolvers/sponsors-info.resolver';

const routes: Routes = [
  {
    path: '',
    component: SponsorsContainerComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        component: SponsorsComponent,
        resolve: {
          sponsors: sponsorsDataResolver
        }
      },
      {
        path: 'info',
        pathMatch: 'full',
        component: SponsorsInfoComponent,
        resolve: {
          sponsorsInfo: sponsorsInfoDataResolver
        }
      }
    ]
  },
];

@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class SponsorsRoutingModule { }
