import { KeyValue, isPlatformBrowser } from '@angular/common';
import { Component, Inject, OnInit, PLATFORM_ID } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-info',
  templateUrl: './info.component.html',
})
export class SponsorsInfoComponent implements OnInit {
  constructor(
    private activatedRoute: ActivatedRoute
  ) {
  }

  data:any;
  sponsorLevelColumnHeadings:string[] = [];

  async ngOnInit() {
    this.data = await this.activatedRoute.snapshot.data["sponsorsInfo"];

    this.setSponsorLevelHeadings();
  }

  setSponsorLevelHeadings() {
    const firstObj = this.data.sponsor_levels[0];

    if (firstObj) {
      this.sponsorLevelColumnHeadings = Object.keys(firstObj).map(s => s.replace("_",' '));
    }
  }

  originalOrder = (a: KeyValue<number,string>, b: KeyValue<number,string>): number => {
    return 0;
  }
}
